import React from 'react'
import { ThemeProvider as MuiThemeProvider, createMuiTheme, ThemeOptions } from '@material-ui/core/styles'
import { ThemeProvider as SCThemeProvider } from 'styled-components';
import { CssBaseline } from '@material-ui/core'
import { scTheme } from '../theming/scTheme'

/**
 * 
 */
type tFontType = 'sys' | 'en' | 'ja'

interface iMyThemeProvider {
  fontType: tFontType
}



type iFONT_FAMIRIES = { [key in tFontType]: string}
const FONT_FAMIRIES: iFONT_FAMIRIES = {
  sys: "'Helvetica Neue', Arial, 'Hiragino Kaku Gothic ProN', 'Hiragino Sans', Meiryo, sans-serif",
  ja: "'Noto Sans JP', sans-serif;",
  en: "'Roboto', sans-serif;",
}

export const MyThemeProvider: React.FC<iMyThemeProvider> = props => {
  const { fontType = 'sys' } = props

  const muiThemeOption = React.useMemo(
    () => {
      let fontFace = FONT_FAMIRIES[fontType]

      const base: ThemeOptions = {
        overrides: {
          MuiCssBaseline: {
            '@global': {
              html: {
                fontFamily: fontFace,
                fontSize: '16px'
              },
              body: {
                fontSize: '1rem'
              }
            }
          }
        },
        typography: {
          fontFamily: 'inherit'
        },
        palette: {
          type: 'dark',
        }
      }
      return base
    },
    [props]
  )

  return (
    <SCThemeProvider theme={{fontType}}>
      <MuiThemeProvider theme={createMuiTheme(muiThemeOption)}>
        <CssBaseline />
        {props.children}
      </MuiThemeProvider>
     </SCThemeProvider>
  )
}